.panou {
    max-width: 100%;
    height: 700px;
    margin-top: -50px;
    margin-left: 20px;
    .panou-0{
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #978a9b ;
        width: 1400px;
        height: 705px;
        flex-wrap: wrap;
        

        margin: 50px;
        .panou1 {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 701px;
            height: 704px;
            position: relative;
            cursor: pointer;
            
            p{
                font-size: 20px;
                font-weight: 600;
                color: white;
                margin-right: 410px;
                margin-bottom: 170px;
                position: absolute;
            }
            h1 {
                color: white;
                width: 500px;
                font-size: 40px;
                word-spacing: -8px;

            }
            button {
                width: 270px;
                height: 55px;
                margin-top: 230px;
                margin-right: 225px;
                border: none;
                border-radius: 2%;
                cursor: pointer;

            }
        }
        .panou2{
            img{
                width: 697px;
                height: 705px;
            }
        }
    }
}